ul.rec-menu-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: auto;
    flex-direction: column;
    justify-content: center;
    gap: 0.4rem;
    width: 600px;
    padding: 2px;
  }

  h2.recipeHeader {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 150px;
    justify-content: center; 
  }

  .rec-prep {
    margin-top: 10%;
  }
  .rec-instructions {
    margin-top: -10%;
  }
  li {
    margin-bottom: 5px;
  }
  .menu-div{
    width: 185px;
    margin: auto; 
  }

  .img-fluid {
    z-index: -1000;
    position: fixed;
    top: 0;
    left: 0;
    min-height: 100vh;
    min-width: 100vw;
  }
  h3 {
    text-align: left;
    /* margin: 0% 130% 0% 130%;  */
    width: 140px;
  }
  .menu-item{
    /* display: flex; */
    width: 500px;
    text-align:left;
    margin-left: auto;
    margin-right: auto;
  }
