body {
  font-family:'verdana';

}

.menuList {
  height: 1500px;
}

h1 {
  margin-top: 150px;
  text-align: center;
}

h1.regularList {
  margin-top: 150px;
  margin-bottom: 50px;
}

.tile {
  width: 310px;
  height: 200px;
  border: 1px solid #ccc;
  margin: 10px;
  margin-top: 10px;
  margin-left: 50px;
  display: inline-block;
  background-image: url("../images/tileImg.jpeg");
  background-size: cover;
}
  
  .tile-content {
    padding: 10px;
    text-align: center;
  }

  .tile-content h3 {
    width: 700px;
    margin: 0;
    display: inline-block;
  }
  
  .tile a {
    text-decoration: none;
    color: #000;
    display: block;
    height: 0px;
    width: 100%;
  }
  .tile p {
    text-align:left;
    margin: 0;
  }
  
  .fetch-button {
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    margin: 20px 0;
  }